<template>
  <div class="box">
    <particular-shop-header></particular-shop-header>
      <!--Countdown kill-->
     <particular-shop-section></particular-shop-section>
      <!--promotion-->
      <particular-shop-promotion></particular-shop-promotion>
      <!--evaluate-->
     <particular-shop-evaluate></particular-shop-evaluate>
      <!--shoplistInfo-->

      <!--introduce-->
      <particular-shop-introduce></particular-shop-introduce>
      <!--explain-->
      <particular-shop-explain></particular-shop-explain>
  </div>
</template>

<script>
    import ParticularShopIntroduce from "./ParticularShopIntroduce";
    import ParticularShopExplain from "./ParticularShopExplain";
    import ParticularShopEvaluate from "./ParticularShopEvaluate";
    import ParticularShopSection from "./ParticularShopSection";
    import ParticularShopPromotion from "./ParticularShopPromotion";
    import ParticularShopHeader from "./particularShopHeader";
    export default {
        name: "particularShopCenter",
      components: {
        ParticularShopHeader,
        ParticularShopPromotion,
        ParticularShopSection, ParticularShopEvaluate, ParticularShopExplain, ParticularShopIntroduce}
    }
</script>

<style scoped>
  @import "../../assets/css/particularShop.css";
  .box{
    width: 100%;
    height: 5.86rem;
    overflow-x: hidden;
    overflow-y: auto;
    float: left;
  }
  .box::-webkit-scrollbar{
    display: none;
  }
</style>



