<template>
  <div class="goodList-right">
    <ul>
      <li>
        <span class="goodli-01">为你优选(20)</span>
        <span class="goodli-02">数量</span>
        <span class="goodli-03">价格</span>
      </li>
    </ul>
      <!--商品列表1-->
    <ul class="ulmax">
        <li :key="index" v-for="(i,index) in listTab">
          <div class="liLeft">
            <img class="liLeftImg" :src="i.listTabImg"/>
          </div>
          <div class="liRight">
            <dl>
              <dt class="liRight-01">{{i.listTab01}}</dt>
              <dd class="liRight-02">
                <strong class="liRight-03">{{i.listTab02}}</strong>
                <strong class="liRight-04">{{i.listTab03}}</strong>
              </dd>
              <dd class="liRight-05">
                <span>{{i.listTab04}}</span>
              </dd>
              <dd>
                <label class="liRight-06">
                    <em>{{i.listTab05}}</em>
                    <span>{{i.listTab06}}</span>
                    <div class="boxmm">
                        <div class="spanbox" v-model="i.isShow" v-show="i.isShow">
                          <span v-on:click="min(i)" class="min"></span>
                          <input type="number"v-model="i.number" class="num" value="0"/>
                        </div>
                        <span v-on:click="show(i)" class="add"></span>
                    </div>
                </label>
                <label class="liRight-07">
                  <em>{{i.listTab07}}</em>
                  <span>{{i.listTab08}}</span>
                </label>
              </dd>
            </dl>
          </div>
        </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "StoreGoodListTabShop",
      data(){
          return{
            isShow:false,
            quality:0,
            listTab:[
              {listTab01:"维达V4626绵韧无芯卷纸120g*12卷/提",listTab02:"月售108件",listTab03:"好评100%",listTab04:"秒杀5.24折",listTab05:"￥",listTab06:"20.9",listTab07:"¥",listTab08:"30.9",listTabImg:"../../../static/images/goodlistshop-01.png",number:0},
              {listTab01:"君乐宝纯享益生菌发酵乳抹茶味330g/瓶",listTab02:"月售108件",listTab03:"好评100%",listTab04:"秒杀5.24折",listTab05:"￥",listTab06:"20.9",listTab07:"¥",listTab08:"30.9",listTabImg:"../../../static/images/goodlistshop-02.png",number:0},
              {listTab01:"蒙牛优益C原味活菌型乳酸菌饮品340ml/瓶",listTab02:"月售108件",listTab03:"好评100%",listTab04:"秒杀5.24折",listTab05:"￥",listTab06:"20.9",listTab07:"¥",listTab08:"30.9",listTabImg:"../../../static/images/goodlistshop-03.png",number:0},
              {listTab01:"华山牧场纯牛奶250ml*12/箱",listTab02:"月售108件",listTab03:"好评100%",listTab04:"秒杀5.24折",listTab05:"￥",listTab06:"20.9",listTab07:"¥",listTab08:"30.9",listTabImg:"../../../static/images/goodlistshop-04.png",number:0},
            ]
          }
      },
      methods:{
          show:function(index){
            // console.log(this);
            index.isShow=true;
            index.number++
          },
          min:function (index) {
            index.number--;
            if(index.number<=0){
              index.number=0;
              index.isShow=false;
            }
          }
      }

    }
</script>

<style scoped>
  @import "../../assets/css/storegood.css";
</style>



