<template>
    <div class="box">
        <my-header></my-header>
        <my-center></my-center>
        <my-footer></my-footer>
    </div>
</template>

<script>
    import MyHeader from "../components/my/MyHeader";
    import MyCenter from "../components/my/MyCenter";
    import MyFooter from "../components/my/MyFooter";
    export default {
        name: "my",
      components: {MyFooter, MyCenter, MyHeader}
    }
</script>

<style scoped>
  @import "../../static/css/my.css";
  .box{
    width:100%;
    height:100%;
    display:flex;
    flex-direction:column;
  }
</style>



