<template>
  <!--header-->
  <div class="head">
      <div class="header">
          <router-link to="/">
              <a href="javascript:;"><img class="heaImg" :src="star.heaImg"></a>
          </router-link>
        <h3>{{star.marke}}</h3>
      </div>
      <!--searchShop-->
      <div class="searchShop">
        <div class="search">
          <a href="#"><i class="iconfont icon-sousuo1"></i>
            <span class="sea-span">{{star.seaSpan}}</span>
          </a>
        </div>
      </div>
  </div>

</template>

<script>
    export default {
        name: "supermarketHeader",
      data() {
        return {
          star: {marke: "超市便利频道",seaSpan:"搜索附近的商家商品",heaImg:"../../../static/images/zuo.png"},
        }

      }
    }
</script>

<style scoped>
    @import "../../../static/css/supermarket.css";
</style>



