<template>
  <div class="header">
    <!--header-01-->
    <div class="header-01">
      <div class="header-01-left" >
        <a href="#"><i class="iconfont icon-weizhiiconx"></i></a>
        <span class="site">{{heade.site}}</span>
      </div>
      <div class="header-01-right">
        <a class="msg-a1" href="#"><i class="iconfont icon-xiaoxi"></i></a>
        <a class="msg-a2" href="#"><i class="iconfont icon-saoyisao"></i></a>
      </div>
    </div>
    <!--search-->
    <div class="search">
      <a class="search-a1" href="#">
        <i class="iconfont icon-sousuo1"></i>
        <span>{{heade.search}}</span>
      </a>
    </div>
    <!--header-list-->
    <div class="header-list" >
      <a href="#" v-for="(i,index) in lis">{{i.ist}}</a>
    </div>
  </div>
</template>

<script>
    export default {
        name: "IndexHeader",
      data(){
        return{
          heade:{site:"立人科技",search:"搞定一切"},
          lis:[
            {ist:"酸奶"},
            {ist:"牛奶"},
            {ist:"儿童蛋糕"},
            {ist:"面包"},
            {ist:"方便面"},
            {ist:"妇科用药"},
            {ist:"卫生纸"},
          ]
        }
      }
    }
</script>

<style scoped>
  @import "../../assets/css/index.css";
</style>



