<template>
  <div class="footer">
      <div class="mp">价格说明</div>
      <p class="mo">
          <strong class="str">划线价格：</strong>
          <span class="span11">划线的价格并非《中华人民共和国价格法》及《禁止价格欺诈行为的规定》等相关法律法规中所指商品“原价”的含义，仅为到家平台商家处商品的供应商指导价或建议零售价，</span>
          <strong class="str">并非商品原价。</strong>
      </p>
      <p class="mo">
          <strong class="str">未划线价格：</strong>
          <span class="span11">未划线的价格指该商家处商品在京东到家平台实时标价，具体成交价格根据商品参加的活动，或者您使用优惠券等发生变化，最终以订单结算页面价格为准。</span>
      </p>
  </div>
</template>

<script>
    export default {
        name: "ParticularShopExplain"
    }
</script>

<style scoped>
  @import "../../assets/css/particularShop.css";
</style>



