    <router-link to="/"><a href="javascript:;"><img class="heaImg zuo2" :src="search.zuo2" alt=""></a></router-link>
    <div class="search">
      <a href="#"><i class="iconfont icon-sousuo1"></i>
        <span>{{search.sousuo}}</span>
      </a>
    </div>
    <a href="javascript:;"><img class="heaImg xin" :src="search.xin" alt=""></a>
    <a href="javascript:;"><img class="heaImg dian" :src="search.dian" alt=""></a>
  </div>
</template>

<script>
    export default {
        name: "StoreGoodHeader",
      data() {
        return {
          search:
            {sousuo: "搜索店内商品，共3501件好物",zuo2:"../../../static/images/zuo2.png",xin:"../../../static/images/xin.png",dian:"../../../static/images/dian.png"}
        }

      }
    }
</script>

<style scoped>
  @import "../../assets/css/storegood.css";
</style>



