import Vue from 'vue'
import Router from 'vue-router'
import index from '../pages/index'
import my from '../pages/my'
import supermarket from '../pages/supermarket'
import ParticularShop from '../pages/ParticularShop'
import StoreGood from '../pages/StoreGood'
import NotLogForm from '../pages/NotLogForm'
import NotLogShop from '../pages/NotLogShop'
import ShopClass from '../pages/ShopClass'

Vue.use(Router)
const routers = new VueRouter({
  routes: router,
  mode: 'history'
})
export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path:'/my',
      name:'my',
      component: my
    },
    {
      path:'/supermarket',
      name:'supermarket',
      component: supermarket
    },
    {
      path:'/particularShop',
      name:'ParticularShop',
      component: ParticularShop
    },
    {
      path:'/StoreGood',
      name:'StoreGood',
      component: StoreGood
    },
    {
      path:'/NotLogForm',
      name:'NotLogForm',
      component: NotLogForm
    },
    {
      path:'/NotLogShop',
      name:'NotLogShop',
      component: NotLogShop
    },
    {
      path:'/ShopClass',
      name:'ShopClass',
      component: ShopClass
    }
  ]
})



