<template>
  <div class="discount">
    <ul>
      <li :key="index" v-for="(i,index) in shopli">
        <a href="javascript:;">
          <img :src="i.shopliImg">
        </a>
      </li>
    </ul>
  </div>
</template>
<script>
    export default {
        name: "IndexDiscount",
      data() {
        return {
          shopli: [
            {shopliImg: "../../../static/images/shop-01.png"},
            {shopliImg: "../../../static/images/shop-02.png"},
          ]
        }
      }
    }
</script>

<style scoped>
  @import "../../assets/css/index.css";
</style>



