            {iconSpan: "帮助中心",iconImg:"../../static/images/my-icon-09.png"},
            {iconSpan: "办公室超市",iconImg:"../../static/images/my-icon-10.png"},
            {iconSpan: "配送员注册",iconImg:"../../static/images/my-icon-11.png"},
            {iconSpan: "关于我们",iconImg:"../../static/images/my-icon-12.png"},
            {iconSpan: "岁末狂欢",iconImg:"../../static/images/my-icon-13.png"},
          ]
        }

      }
    }
</script>

<style scoped>
    @import "../../../static/css/my.css";
</style>



