<template>
  <!--banner-->
  <div class="banner">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide"  :key="index" v-for="(i,index) in ban">
          <a href="javascript:;"><img :src="i.banImg"/></a>
        </div>
      </div>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>
    </div>
  </div>
</template>

<script>
  import Swiper from "../../../static/js/swiper.min.js";
  export default {
    name: "particularShopHeader",
    data() {
      return {
        ban: [
          {banImg: "../../../static/images/particular-01.webp"},
          {banImg: "../../../static/images/particular-02.webp"},
          {banImg: "../../../static/images/particular-03.webp"},
          {banImg: "../../../static/images/particular-04.webp"},
          {banImg: "../../../static/images/particular-05.webp"}
        ]
      }

    },
    // 轮播图插件
    mounted: function () {
      var swiper = new Swiper('.swiper-container', {
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction',
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
      });

    }

  }
</script>

<style scoped>
  @import "../../assets/css/swiper.min.css";
  @import "../../assets/css/particularShop.css";
</style>



