<template>
  <div class="showShop">
    <div class="showShopAuto">
      <a  :key="index" v-for="(i,index) in showp" href="javascript:;"><img :src="i.showpImg"/></a>
    </div>
  </div>
</template>

<script>
    export default {
        name: "IndexShowShop",
      data() {
        return {
          showp: [
            {showpImg: "../../../static/images/show-01.png"},
            {showpImg: "../../../static/images/show-02.png"},
            {showpImg: "../../../static/images/show-03.png"}
          ]
        }

      }
    }
</script>

<style scoped>
  @import "../../assets/css/index.css";
</style>



