<template>
  <div class="banner">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" :key="index" v-for="(i,index) in ban">
            <a href="#"><img alt="" :src="i.banImg"></a>
        </div>
      </div>
      <div class="swiper-pagination"></div> <!--轮播序号-->
    </div>
  </div>
</template>

<script>
  import Swiper from "../../../static/js/swiper.min.js";
    export default {
        name: "supermarketBanner",
      data() {
        return {
          ban: [
            {banImg: "../../../static/images/super-01.png"},
            {banImg: "../../../static/images/super-02.png"}
          ]
        }
      },
      // 轮播图插件
      mounted: function () {
        new Swiper('.swiper-container', {
          loop: true, // 循环模式选项
          autoplay: {
            delay: 3000,
            stopOnLastSlide: false,
            disableOnInteraction: true,
          },
          spaceBetween: 0,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          }
        })

      }
    }
</script>

<style scoped>
    @import "../../../static/css/supermarket.css";
    @import "../../../static/css/swiper.minxg.css";
</style>



